.CarSearch__label {
  font-size: 1rem;
  font-weight: bold;
  display: block;
  margin-bottom: var(--spacing);
}

.CarSearch__input,
.CarSearch__button {
  font-size: 1rem;
  padding: calc(var(--spacing) / 2) var(--spacing);
  margin-bottom: var(--spacing);
  border: 1px solid;
}

.CarSearch__input {
  margin-right: var(--spacing);
  color: var(--black);
}

.CarSearch__button {
  background-color: var(--blue);
  border-color: var(--blue);
  color: var(--white);
  opacity: 0.85;
  cursor: pointer;
  -webkit-transition: opacity 0.1s linear;
  -o-transition: opacity 0.1s linear;
  transition: opacity 0.1s linear;
}

.CarSearch__button:hover {
  opacity: 1;
}



